.location-input {
  display: -ms-flexbox;
  display: flex;
}

.location-input__input {
  width: 100%;
  border: none;
  padding: .8rem;
  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
  -webkit-appearance: none;
  border-radius: 0;
  margin: 0;
  display: block;
}

.location-input__icon, .overlay__icon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.location-input__icon {
  padding-left: 1rem;
  border-bottom: 1px solid #eee;
}

.location-input__overlay {
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
}

.overlay__icon .fa {
  color: white;
}

.overlay__top-bar {
  background-color: #EE1844;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  padding: .5rem .8rem;
}

.overlay__input {
  border: none;
  border-radius: .1rem;
  padding: .4rem;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 0 .8rem;
}

.overlay__cancel {
  border: none;
  background-color: transparent;
  color: white;
  padding: 0;
}



